import React, { useState } from "react";
import { useNavigate } from "react-router-dom"; //    React Router

function Report() {
  const [formData, setFormData] = useState({
    productId: "",
    reason: "",
    details: "",
  });

  const [submitted, setSubmitted] = useState(false);
  const navigate = useNavigate(); // URL   Hook

  //   
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  //   
  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("  :", formData);
    setSubmitted(true); //    
  };

  //   
  const handleClose = () => {
    navigate("/"); //   
  };

  return (
    <div
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        zIndex: 1000,
      }}
    >
      <div
        style={{
          width: "500px",
          backgroundColor: "white",
          borderRadius: "10px",
          padding: "20px",
          boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
        }}
      >
        <h2 style={{ textAlign: "center" }}> </h2>
        {submitted ? (
          <div style={{ textAlign: "center", marginTop: "20px" }}>
            <h3>  .</h3>
            <p>  .</p>
            <button
              onClick={handleClose} //     URL 
              style={{
                marginTop: "10px",
                padding: "10px 20px",
                backgroundColor: "#007BFF",
                color: "white",
                border: "none",
                borderRadius: "5px",
                cursor: "pointer",
              }}
            >
              
            </button>
          </div>
        ) : (
          <form onSubmit={handleSubmit}>
            <div style={{ marginBottom: "15px" }}>
              <label htmlFor="productId" style={{ display: "block", marginBottom: "5px" }}>
                 ID:
              </label>
              <input
                type="text"
                id="productId"
                name="productId"
                value={formData.productId}
                onChange={handleChange}
                required
                style={{
                  width: "100%",
                  padding: "10px",
                  border: "1px solid #ccc",
                  borderRadius: "5px",
                }}
              />
            </div>
            <div style={{ marginBottom: "15px" }}>
              <label htmlFor="reason" style={{ display: "block", marginBottom: "5px" }}>
                 :
              </label>
              <select
                id="reason"
                name="reason"
                value={formData.reason}
                onChange={handleChange}
                required
                style={{
                  width: "100%",
                  padding: "10px",
                  border: "1px solid #ccc",
                  borderRadius: "5px",
                }}
              >
                <option value="">  </option>
                <option value=" "> </option>
                <option value=" "> </option>
                <option value=" "> </option>
                <option value=""></option>
              </select>
            </div>
            <div style={{ marginBottom: "15px" }}>
              <label htmlFor="details" style={{ display: "block", marginBottom: "5px" }}>
                 :
              </label>
              <textarea
                id="details"
                name="details"
                value={formData.details}
                onChange={handleChange}
                required
                rows="5"
                style={{
                  width: "100%",
                  padding: "10px",
                  border: "1px solid #ccc",
                  borderRadius: "5px",
                  resize: "none",
                }}
              ></textarea>
            </div>
            <button
              type="submit"
              style={{
                width: "100%",
                padding: "10px",
                backgroundColor: "#007BFF",
                color: "white",
                border: "none",
                borderRadius: "5px",
                cursor: "pointer",
              }}
            >
               
            </button>
          </form>
        )}
      </div>
    </div>
  );
}

export default Report;