import React, { useState } from "react";
import DeliveryPopup from "./DeliveryPopup"; // DeliveryPopup  

function DeliveryList() {
  const [addresses, setAddresses] = useState([
    { id: 1, name: "", address: "   123", phone: "010-1234-5678" },
    { id: 2, name: "", address: "   456", phone: "010-5678-1234" },
  ]);
  const [isPopupOpen, setIsPopupOpen] = useState(false);

  //  
  const openPopup = () => {
    setIsPopupOpen(true);
  };

  //  
  const closePopup = () => {
    setIsPopupOpen(false);
  };

  //   
  const handleSaveAddress = (newAddress) => {
    const updatedAddresses = [
      ...addresses,
      { id: addresses.length + 1, ...newAddress },
    ];
    setAddresses(updatedAddresses);
  };

  //  
  const handleDeleteAddress = (id) => {
    const updatedAddresses = addresses.filter((item) => item.id !== id);
    setAddresses(updatedAddresses);
  };

  return (
    <div style={{ padding: "20px", fontFamily: "Arial, sans-serif" }}>
      <h1 style={{ textAlign: "center" }}> </h1>

      {/*   */}
      <table style={{ width: "100%", borderCollapse: "collapse", marginTop: "20px" }}>
        <thead>
          <tr style={{ borderBottom: "2px solid #ccc" }}>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
            <th style={{ padding: "10px", textAlign: "left" }}></th>
            <th style={{ padding: "10px", textAlign: "left" }}></th>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
          </tr>
        </thead>
        <tbody>
          {addresses.map((item) => (
            <tr key={item.id} style={{ borderBottom: "1px solid #ddd" }}>
              <td style={{ padding: "10px", textAlign: "center" }}>{item.id}</td>
              <td style={{ padding: "10px" }}>{item.name}</td>
              <td style={{ padding: "10px" }}>{item.address}</td>
              <td style={{ padding: "10px", textAlign: "center" }}>{item.phone}</td>
              <td style={{ padding: "10px", textAlign: "center" }}>
                <button
                  onClick={() => handleDeleteAddress(item.id)}
                  style={{
                    padding: "5px 10px",
                    backgroundColor: "#FF6347",
                    color: "white",
                    border: "none",
                    borderRadius: "5px",
                    cursor: "pointer",
                  }}
                >
                  
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/*    */}
      <div style={{ textAlign: "right", marginTop: "20px" }}>
        <button
          onClick={openPopup}
          style={{
            padding: "10px 20px",
            backgroundColor: "#007BFF",
            color: "white",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
          }}
        >
           
        </button>
      </div>

      {/*   */}
      <DeliveryPopup
        isOpen={isPopupOpen}
        onClose={closePopup}
        onSave={handleSaveAddress}
      />
    </div>
  );
}

export default DeliveryList;